<!--
 * @Description: 编辑弹窗
 * @Autor: YML
 * @Date: 2021-05-14 11:53:32
 * @LastEditors: YML
 * @LastEditTime: 2021-05-19 09:01:07
-->
<template>
  <div class="edit-modal">
    <modal
      width="44.58vw"
      height="30.78vw"
      :title="title"
      @close="$emit('close')"
      :visible="visible"
    >
      <img
        slot="modalBg"
        class="img-edit"
        src="../../assets/img/components/img-edit2.png"
        alt=""
      />
      <a-form-model
        layout="inline"
        class="form-edit"
        ref="ruleForm"
        :model="form"
        :rules="rules"
      >
        <a-form-model-item label="班组名称：" prop="groupName">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择班组名称"
            :disabled="isShow"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              班组名称1
            </a-select-option>
            <a-select-option value="2">
              班组名称2
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="值班领导：" prop="leader">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择值班领导"
            :disabled="isShow"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              张三
            </a-select-option>
            <a-select-option value="2">
              李四
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="联系方式：" prop="phone">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择联系方式"
            :disabled="isShow"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              15698745629
            </a-select-option>
            <a-select-option value="2">
              15698745629
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="值班人员：" prop="operator">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择值班人员"
            :disabled="isShow"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              人员1，人员2
            </a-select-option>
            <a-select-option value="2">
              人员1，人员2
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="开始日期：" prop="startTime">
          <a-date-picker
            class="default-date-picker"
            show-time
            dropdown-class-name="default-date-picker-dropdown"
            :disabled="isShow"
            :get-calendar-container="triggerNode => triggerNode.parentNode"
          />
        </a-form-model-item>
        <a-form-model-item label="结束日期：" prop="endTime">
          <a-date-picker
            class="default-date-picker"
            show-time
            dropdown-class-name="default-date-picker-dropdown"
            :disabled="isShow"
            :get-calendar-container="triggerNode => triggerNode.parentNode"
          />
        </a-form-model-item>
        <!-- <a-from-model-item label="开始时间：">
          <a-time-picker :open="open" @openChange="handleOpenChange">
            <a-button
              slot="addon"
              size="small"
              type="primary"
              :getPopupContainer="triggerNode => triggerNode.parentNode"
              @click="handleClose"
            >
              确定
            </a-button>
          </a-time-picker>
        </a-from-model-item> -->
        <!-- <a-from-model-item label="结束时间：">
          <a-time-picker :open.sync="open2">
            <a-button
              slot="addon"
              size="small"
              type="primary"
              :getPopupContainer="triggerNode => triggerNode.parentNode"
              @click="handleClose"
            >
              确定
            </a-button>
          </a-time-picker>
        </a-from-model-item> -->
        <a-form-model-item label="备注">
          <a-textarea
            class="default-textarea"
            placeholder="请填写"
            :disabled="isShow"
          />
        </a-form-model-item>
      </a-form-model>
      <div class="line" v-show="!isShow"></div>
      <!-- 取消和保存按钮 start -->
      <div class="operation-btn" v-show="!isShow">
        <a-button type="default" class="form-button" @click="$emit('close')"
          >取消</a-button
        >
        <a-button type="primary" class="form-button save-button" @click="save">保存</a-button>
      </div>
      <!-- 取消和保存按钮 end -->
    </modal>
  </div>
</template>

<script>
import Modal from "../../components/Modal";
export default {
  data() {
    return {
      open: false,
      open2: false
    };
  },
  components: {
    Modal //弹窗组件
  },
  props: {
    // 对话框是否可见
    visible: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      form: {
        groupName: "",
        leader: "",
        operator: "",
        startTime: "",
        endTime: ""
      },
      rules: {
        groupName: [
          {
            required: true,
            message: "请选择班组名称！",
            trigger: "blur"
          }
        ],
        rank: [
          {
            required: true,
            message: "请选择值班领导！",
            trigger: "blur"
          }
        ],
        phone: [
          {
            required: true,
            message: "请选择联系方式！",
            trigger: "blur"
          }
        ],
        leader: [
          {
            required: true,
            message: "请选择值班领导！",
            trigger: "blur"
          }
        ],
        operator: [
          {
            required: true,
            message: "请选择值班人员！",
            trigger: "blur"
          }
        ],
        startTime: [
          {
            required: true,
            message: "请选择开始时间！",
            trigger: "blur"
          }
        ],
        endTime: [
          {
            required: true,
            message: "请选择结束时间！",
            trigger: "blur"
          }
        ]
      },
      isShow: this.title == "查看详情"
    };
  },
  methods: {
    save() {
      console.log(this.form);
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    handleOpenChange(open) {
      console.log("open", open);
      this.open = open;
    },
    handleClose() {
      this.open = false;
      this.open2 = false;
    }
  }
};
</script>

<style lang="less"></style>
